import { h, Host } from "@stencil/core";
export class RaulPopoverContent {
    render() {
        return (h(Host, null,
            h("raul-popover-icon", null,
                h("slot", { name: "icon" })),
            h("raul-popover-col", null,
                h("raul-popover-label", null,
                    h("slot", { name: "label" }),
                    h("slot", { name: "sub-label" })),
                h("raul-popover-status", null,
                    h("slot", { name: "status" })),
                h("raul-popover-button", null,
                    h("slot", { name: "button" })),
                h("raul-popover-custom", null,
                    h("slot", null)))));
    }
    static get is() { return "raul-popover-content"; }
}
